.scherohome {
    padding: 233px 0 167px;
    &-content-wrap {
        grid-column: 2/8;
        .--text-14 {
            color: $black60-cl;
        }
        .--h1 {
            margin-bottom: 40px;
            margin-left: -5px;
        }
        p.--text-16 {
            margin-bottom: 20px;
            padding-right: 100px;          
        }
    }
    &-slide-container {
        grid-column: 9/13;
        position: relative;
        @include size(100%);
        .slide-bg-wrap {
            @include fullscreen(false);
            .bg-circle-solid {
                width: 96%;
                height: 0;
                padding-bottom: 96%;
                background-color: $green-cl;
                @include mid(false);
                @include br(50%);
                .bg-dotted-outer {
                    position: absolute;
                    width: 200%;
                    height: 200%;
                    @include mid(false);
                    img {
                        @include mid(false);
                    }
                }
                .bg-circle-blur {
                    position: absolute;
                    width: 180%;
                    height: 180%;
                    @include mid(false);
                    img {
                        @include mid(false);
                    }
                }
            }
        }
    }
    .swiper {
        @include size(100%);
        position: relative;
        .swiper-slide {
            @include mid(true);
            img {
                max-width: 100%;
            }
        }        
    }
    .swiper-pagination {
        bottom: -60px;
        height: 8px;
        left: 50%;
        @include transform(translateX(-50%));
        .swiper-pagination-bullet {
            background-color: #C4C4C4;
            @include size(8px);
            margin: 0 5px;
            @include transition($t);
            &:hover {
                background-color: $blue-cl;
            }
        }
        .swiper-pagination-bullet-active {
            background-color: $blue-cl;
        }
    }
}

.scabouthome {
    &-bg-wrap {
        width: 100%;
        height: 520px;
        overflow: hidden;
        background: url('../img/about-bg.jpg') no-repeat center 56%;
        background-size: cover;
        margin-bottom: 152px;
    }
    .--h2 {
        grid-column: 2/5;
        margin-right: -32px;
    }
    &-content-wrap {
        grid-column: 7/13;
        .--text-20 {
            margin-bottom: 24px;
        }
        .--text-16 {
            margin-bottom: 26px;
            &:nth-last-child() {
                margin-bottom: 24px;
            }
        }
        margin-bottom: 117px;
    }
    .line {
        grid-column: 2/13;
    }
}
.scproducthome {
    padding-top: 80px;
    padding-bottom: 132px;
    margin-bottom: 182px;
    &-title-wrap {
        grid-column: 2/11;
        .--text-14 {
            text-transform: uppercase;
            margin-bottom: 8px;
        }
        .--h2 {
            margin-bottom: 80px;
        }
    }
    &-product-con {
        grid-column: 2/11;
        grid-row: 2/3;
        @include size(100%);
        .product-wrap {
            // margin-left: -16px;
            // margin-right: -16px;
            // width: calc(100% + 32px);
            // display: flex;
            // flex-wrap: nowrap;
            .product-item-wrap {
                
                .item-img-wrap {
                    height: 294px;
                    background-color: $black05-cl;
                    margin-bottom: 24px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    img {
                        max-width: 50%;
                    }
                }
                .line {
                    background-color: $green-cl;
                    height: 3px;
                }
                .item-content-wrap {
                    margin-top: 12px;
                    .--h5 {
                        margin-bottom: 12px;
                    }
                    p.--text-14 {
                        margin-bottom: 12px;
                    }
                    
                }
            }
        }
        .swiper {
            @include size(100%);
        }
    }
    .swiper-product-nav {
        grid-column: 11/13;
        grid-row: 1/2;
        margin-top: auto;
        margin-bottom: 90px;
        .swiper-nav-wrap {
            display: flex;
            justify-content: flex-end;
            height: 44px;
            .swiper-btn-prev,
            .swiper-btn-next {
                @include size(44px);
                background-color: $black05-cl;
                @include mid(true);
                @include br(50%);
                margin-left: 16px;
                cursor: pointer;
                @include transition($t);
                svg path {
                    @include transition($t);
                }
                &:hover {
                    background-color: $green-cl;
                    svg path {
                        fill: #ffffff;
                        fill-opacity: 1;
                    }
                }
            }
        }
    }
}
.scservicehome {
    margin-top: 182px;    
    padding-bottom: 128px;
    background-color: $blue-cl;
    .line {
        width: 100%;
        height: 10px;
        background-color: $green-cl;
    }
    &-bg-wrap {
        grid-column: 1/8;
        grid-row: 1/2;
        margin-left: -136px;
        height: 532px;
        margin-top: -192px;
    }
    &-content-wrap {
        grid-column: 9/13;
        grid-row: 1/3;
        padding-top: 136px;
        .--text-14 {
            text-transform: uppercase;
            color: $white-cl;
            margin-bottom: 8px;
        }
        .--h3 {
            color: $white-cl;
            margin-bottom: 64px;
        }
        p.--text-16 {
            color: $white70-cl;
            margin-bottom: 26px;
            &:nth-last-child() {
                margin-bottom: 23px;
            }
        }
        a.text-link {
            color: $green-cl;
        }
    }
    &-quote-wrap {
        grid-column: 2/8;
        grid-row: 2/3;
        display: flex;
        align-items: flex-start;
        img {
            margin-left: 33px;
            margin-right: 62px;
        }
        .--h5 {
            color: $white-cl;
        }
    }
}