/*primary colors*/
$green-cl: rgba(90,179,121,1);
$blue-cl: rgba(0,83,154,1);
$blue-cl-10: rgba(0,83,154,.1);
$blue-cl-05: rgba(0,83,154,.05);

$white-cl: #fff;
$white80-cl: rgba(255, 255, 255, 0.8);
$white70-cl: rgba(255, 255, 255, 0.7);
$white50-cl: rgba(255, 255, 255, 0.5);
$white40-cl: rgba(255, 255, 255, 0.4);
$white30-cl: rgba(255, 255, 255, 0.3);
$white10-cl: rgba(255, 255, 255, 0.1);
$white05-cl: rgba(197, 112, 112, 0.05);

$black-cl: rgba(2,2,2,1);
$black90-cl: rgba(2,2,2,.9);
$black80-cl: rgba(2,2,2,.8);
$black70-cl: rgba(2,2,2,.7);
$black60-cl: rgba(2,2,2,.6);
$black50-cl: rgba(2,2,2,.5);
$black40-cl: rgba(2,2,2,.4);
$black30-cl: rgba(2,2,2,.3);
$black20-cl: rgba(2,2,2,.2);
$black10-cl: rgba(2,2,2,.1);
$black05-cl: rgba(2,2,2,.05);

// border-color
$border-cl: #d0d0d0;

/*headings typo*/
$fs-h1: 8rem;
$lh-h1: 8.4rem;
$fs-h2: 5.6rem;
$lh-h2: 6.7rem;
$fs-h3: 4.8rem;
$lh-h3: 5.8rem;
$fs-h4: 3.2rem;
$lh-h4: 3.8rem;
$fs-h5: 2rem;
$lh-h5: 3rem;

$fs-body: 2rem;
$lh-body: 3rem;

$fs-body-s: 1.6rem;
$lh-body-s: 2.6rem;

$fs-body-xs: 1.4rem;
$lh-body-xs: 2.2rem;

$fs-body-xxs: 1.2rem;
$lh-body-xxs: 1.4rem;

// timing transition
$t: 0.5s;
$cubic: cubic-bezier(0.59, 0.23, 0.22, 0.96);

/*font families*/
$font-main: br;

//============================================================//

/*BOXED LAYOUT*/
$container-max-width: 1232px;
$container-width: 1168px; // tùy thuộc vào container của bản thiết kế mà chỉnh sửa cho hợp lý

/*GRID - media queries breakpoints*/
$xxs-min: 576px; // CÓ THỂ TÙY CHỈNH
$xs-min: 768px; // KHÔNG THAY ĐỔI
$sm-min: 992px; // KHÔNG THAY ĐỔI
$md-min: 1280px; // KHÔNG THAY ĐỔI
$lg-min: 1450px; // CÓ THỂ TÙY CHỈNH

$xxs-max: ($xxs-min - 1); // Small Mobile
$xs-max: ($xs-min - 1); // Mobile
$sm-max: ($sm-min - 1); // Tablet
$md-max: ($md-min - 1); // Desktop
$lg-max: ($lg-min - 1); // Large Desktop
