.footer {
    padding-top: 86px;
    padding-bottom: 105px;
    &-logo-wrap {
        grid-column: 1/4;
        img {
            margin-bottom: 24px;
            height: 42px;
        }
    }
    &-links-wrap {
        grid-column: 6/8;
        display: flex;
        flex-direction: column;
        a.footer-link {
            margin-bottom: 12px;
            @include text(1.2rem, bs, 2.2rem, $black-cl, uppercase);
            @include transition($t);
            &.active,
            &:hover {
                color: $blue-cl;
            }
        }
    }
    &-contact-wrap {
        grid-column: 9/13;
        margin-bottom: 52px;
        .contact-address {
            color: $black90-cl;
            margin-bottom: 20px;
            span {
                font-family: bs;
            }
        }
        .contact-phone,
        .contact-mail {
            margin-bottom: 20px;
            p.--text-12 {
                display: block;
            }
            a.text-link {
                color: $black-cl;
            }
        }
    }
    &-copy-wrap {
        grid-column: 6/13;
        grid-row: 2/3;
        @include col(7);
        column-gap: 32px;
        .line {
            grid-column: 1/8;
            grid-row: 1/2;
            margin-bottom: 29px;
        }
        .copy {
            grid-column: 1/4;
            color: $black70-cl;
        }
        .privacy-wrap {
            grid-column: 4/6;
            a.text-link {
                grid-column: 4/6;
                color: $black70-cl;
                @include transition($t);
                display: block;
                width: 84px;
                &:hover {
                    color: $black-cl;
                }
            }
        }
        
        .lang-wrap {
            grid-column: 6/8;
            display: flex;
            justify-content: flex-end;
            .lang-eng {
                margin-right: 25px;
            }
            .lang-eng,
            .lang-vie {
                color: $black70-cl;
                &.active,
                &:hover {
                    color: $blue-cl;
                }
            }
        }

    }
}