.header {
    padding: 44px 0 32px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    .container {
        padding: 0 40px;
        .logo-wrap {
            grid-column: 1/2;
            margin-left: -96px;
            margin-top: 7px;
        }
        .header-links-wrap {
            grid-column: 2/6;
            @include col(2);
            column-gap: 32px;
            .header-link {
                @include text(1.2rem, bs, 2.2rem, $black-cl, uppercase);
                margin-bottom: 12px;
                display: inline-block;
                @include transition($t);
                &:hover {
                    color: $green-cl;
                }
            }
        }
        .header-contact-wrap {
            grid-column: 9/13;
            @include col(2);
            column-gap: 32px;
            .--text-12 {
                display: block;
            }
            .contact-link {
                display: inline-block;
            }
        }
    }    
}