@-ms-viewport {
    width: device-width;
}
* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    box-sizing: border-box;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    user-select: none;
    -webkit-user-select: none;

}
html {
    font-size: 62.5%;
}
body {
    // disable zoom on iOS 14
    touch-action: pan-y;
    //
    width: 100%;
    margin: auto;
    @include text($fs-body, $font-main, $lh-body, $black-cl, none);
    background: $white-cl;
    // main {
    //     position: relative;
    //     z-index: 10;
    // }
    .container {
        width: 100%;
        padding-right: 32px;
        padding-left: 32px;
        margin-right: auto;
        margin-left: auto;
        max-width: $container-max-width;
        &.--grid {
            display: grid;
            grid-template-columns: repeat(12, 1fr);
            grid-auto-rows: auto;
            column-gap: 32px;
        }
        @include sm {
            &.--grid {
                grid-template-columns: repeat(4, 1fr);
                column-gap: 16px;
            }
        }
    }
    .--grid {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-auto-rows: auto;
        column-gap: 32px;
        @include sm {
            grid-template-columns: repeat(4, 1fr);
            column-gap: 16px;
        }
    }

    .container-fluid {
        width: 100%;
        padding-right: 32px;
        padding-left: 32px;
        margin-right: auto;
        margin-left: auto;
        @include xs {
            padding-right: 16px;
            padding-left: 16px;
        }
    }
}
