// css button, title, icon, elements...
img {
    max-width: 100%;
    display: block;
}
strong {
    font-family: bb;
}
a {
    font-size: $fs-body-s;
    text-decoration: none;
    color: $black-cl;
    outline: 0;
}

////////////////////// HEADING ///////////////////////

.heading {
    font-family: bs;
    display: inline-block;
    &.--h1 {
        font-family: bb;
        font-size: $fs-h1;
        line-height: $lh-h1;
    }
    &.--h2 {
        font-size: $fs-h2;
        line-height: $lh-h2;
    }
    &.--h3 {
        font-size: $fs-h3;
        line-height: $lh-h3;
    }
    &.--h4 {
        font-size: $fs-h4;
        line-height: $lh-h4;
    }
    &.--h5 {
        font-size: $fs-h5;
        line-height: $lh-h5;
    }
    &.--white {
        color: $white-cl;
    }
    &.--black {
        color: $black-cl;
    }
    @include sm {
        &.--h1 {
            font-size: 12.7rem;
            line-height: 11.3rem;
            bottom: -10px;
            img {
                height: 107px;
            }
        }
        &.--h2 {
            font-size: 4.6rem;
            line-height: 5.5rem;
        }
        &.--h3 {
            font-size: 4.6rem;
            line-height: 5.5rem;
        }
        &.--h4 {
            font-size: 3.2rem;
            line-height: 4.2rem;
        }
        &.--mainheading {
            display: none;
        }
    }
}

////////////////////// BODY TEXT ///////////////////////
.--text-20, .text--16, .--text-14, .--text-12 {
    display: inline-block;
}
.--text-20 {
    font-size: $fs-body;
    line-height: $lh-body;
}
.--text-16 {
    font-size: $fs-body-s;
    line-height: $lh-body-s;
    &.--bold {
        font-family: bs;
    }
}
.--text-14 {
    font-size: $fs-body-xs;
    line-height: $lh-body-xs;
    &.--bold {
        font-family: bs;
    }
}
.--text-12 {
    font-size: $fs-body-xxs;
    line-height: $lh-body-xxs;
}

////////////////////// LABEL ///////////////////////
.--text-up {
    font-family: ib;
    text-transform: uppercase;
    font-size: $fs-body-xxs;
    line-height: $fs-body-xxs;
    color: $black-cl;
    &.--white {
        color: $white-cl;
    }
    &.--grey {
        color: $black40-cl;
    }
}

////////////////////// Link & button ///////////////////////
.text-link {
    color: $blue-cl;
    display: inline-block;
}
.line {
    height: 1px;
    background-color: $black20-cl;
}
.--underline {
    position: relative;
    &:after {
        content: "";
        width: 100%;
        height: 1px;
        background: $blue-cl;
        position: absolute;
        bottom: 0px;
        left: 0;
        //mix-blend-mode: difference;
        @include transform(scaleX(0));
        @include transform-origin(right);
        @include transition($t transform $cubic);
    }
    &:hover,
    &.active {
        &:after {
            @include transform(scaleX(1));
            @include transform-origin(left);
            @include transition($t transform $cubic);
        }
    }
    &.--green {
        &::after {
            background: $green-cl;
        }
    }
    &.--black {
        &::after {
            background: $black-cl;
        }
    }
}
